import React from 'react';
import ColorSelector from './ColorSelector';

function ProductOptionsController() {
  return (
    <div className="product-options-controller">
      <ColorSelector
        options={[
          { value: 'red', color: 'red' },
          { value: 'yellow', color: 'yellow' }
        ]}
      />
    </div>
  );
}

export default ProductOptionsController;
